body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-image: linear-gradient(to top, #a8edea 0%, #fed6e3 100%);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.login{
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  margin-top: 10rem;
}

.login input{
  width: 25em;
  margin: 10px;
  padding: 10px 20px;
  border-radius: 10px;
  border: 1px solid black;
}

.login__button{
  padding: 10px 25px;
  width: 12em;
  border: 1px solid black;
  border-radius: 10px;
  color: black;
  background-color: rgb(255, 214, 221);
  font-size: 16px;
  font-weight: 700;
  letter-spacing: 2px;
  margin-top: 15px;
  cursor: pointer;
}

.container{
  padding: 0 20px;
  margin: 0 auto;
  max-width: 1240px;
  box-sizing: border-box;
  height: 100vh;
}
.flex{
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
.header{
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 5rem;
}
.button{
  background-color: #248ffb;
  border: 1px solid #248ffb;
  color: white;
  padding:10px 20px;
  border-radius: 10px;
  cursor: pointer;
  margin-left: 20px;
}
.input{
  padding: 15px 0px;
  border-radius: 20px;
  border: 1px solid black;
  margin: 0px 20px;
  text-align: center;
  width: 20rem;
  font-weight: 700;
  font-size: 16px;
}
.input:focus{
  outline: none;
}

.select{
  padding: 5px;
  border-radius: 5px;
  border: 1px solid black;
  font-weight: 700;
  margin-left: 10px;
}

.item__list{
  background-color: white;
  border-radius: 50px;
  height:fit-content;
  width: 50vh;
  font-size: 22px;
  margin-top: 5rem;
  text-align: start;


}

.item__wrapper{
  display: flex;
  justify-content: center;
  align-items:center;
  margin: 15px 0;
}

.button__section{
  margin:0 20px;
}

.completed{
  text-decoration-line: line-through;
  opacity: 0.5;
}